.profile {
    text-align: center;
}

.profile img {
    border: 10px solid #bdbdbd;
    border-radius: 50%;
    max-width: 500px;
}

.profile h2,
.profile p {
    text-align: center;
    color: #2e2c2d;
}
.repo {
    background-color: #fbfbfb;
    padding: 10px;
    margin-top: 10px;
    border-radius: 10px;
}

.repo:first-of-type {
    margin-top: 0px;
}

.repo-header {
    display: flex;
}

.repo-header a {
    color: #2e2c2d;
}

.repo-header img {
    width: 32px;
    margin-right: 10px;
}


.links ul {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}

.links li {
    max-width: 128px;
}
